body {
  font-family: 'Lato' ;
  font-size: 18px;
  /*background: linear-gradient(90deg, #00C9FF 0%, #92FE9D 100%);*/
  background: linear-gradient(90deg, #dfe9f3 0%, white 100%);
  
}

p {
  font-family: 'Lato';
  text-align: justify;
  padding:5px;
}
.maincontainer{
  overflow: auto;
  display: block;
}

.page-body {
  margin: auto;
  padding: 5px 15%;
  padding-bottom: 5%;
}

h1{
  text-align: center;
  padding:5px;
}
h2{
  text-align: center;
  padding:5px;
}
button {
  padding: 16px;
  font-family: 'Lato';
  font-size: 16px;
  background-color:  rgb(4, 118, 184);
  color: white;
  border-radius: 9px;
  cursor: pointer;
}

.like{
  border-radius: 50%;
  width:20px;
}

.prof{
  margin: auto;
  width:100%;
  padding-bottom: 20px;

}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: sticky;
  top: 0;
  width: 100%;
  background: white;

}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: rgb(4, 118, 184);
  color: white;
  text-decoration: none;
}


.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
  display: block;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: auto;
}

#upvotes{
  display: block;
  border-bottom: 2px solid black;
  padding:5px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.react-slideshow-zoom-wrapper{
  position: relative;
  z-index: -1;
}

.col-sm-4 {
  z-index: -1;
}

footer{
  position: fixed;
  width:100%;
  bottom:0;
  text-align: center;
  background: rgb(2,0,36);
  background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(158,241,242,0.4096989137451855) 0%);
  padding: 5px;
}
